table,
th,
td {
  border: 1px solid #9f1d48;
  text-align: left;
  border-collapse: collapse;
}

table {
  width: 70%;
  margin: auto;
}

table:nth-child(even) {
  background: lightgray;
}

th,
td {
  padding: 1%;
  width: 15%;
}

th {
  background: #9f1d48;
  color: #ffffff;
}

span {
  visibility: hidden;
}

tr:hover span {
  visibility: visible;
}
